<template>
  <section class="card form">
    {{item}}
    <form @submit.prevent="addFundWeek">
      a
      <FundFormItem v-if="inside"
        :b0.sync="item.a.btc"
        :b1.sync="item.a.btc1"
        :b2.sync="item.a.btc2"
        :b3.sync="item.a.btc3"
        :e0.sync="item.a.eth"
        :e1.sync="item.a.eth1"
        :e2.sync="item.a.eth2"
        :e3.sync="item.a.eth3"
        :u0.sync="item.a.usdt"
        :u1.sync="item.a.usdt1"
        :u2.sync="item.a.usdt2"
        :u3.sync="item.a.usdt3"
      />
      <FundFormItem v-else
        :b0.sync="ab0"
        :b1.sync="ab1"
        :b2.sync="ab2"
        :b3.sync="ab3"
        :e0.sync="ae0"
        :e1.sync="ae1"
        :e2.sync="ae2"
        :e3.sync="ae3"
        :u0.sync="au0"
        :u1.sync="au1"
        :u2.sync="au2"
        :u3.sync="au3"
      />
      k
      <FundFormItem v-if="inside"
        :b0.sync="item.k.btc"
        :b1.sync="item.k.btc1"
        :b2.sync="item.k.btc2"
        :b3.sync="item.k.btc3"
        :e0.sync="item.k.eth"
        :e1.sync="item.k.eth1"
        :e2.sync="item.k.eth2"
        :e3.sync="item.k.eth3"
        :u0.sync="item.k.usdt"
        :u1.sync="item.k.usdt1"
        :u2.sync="item.k.usdt2"
        :u3.sync="item.k.usdt3"
      />
      <FundFormItem v-else
        :b0.sync="kb0"
        :b1.sync="kb1"
        :b2.sync="kb2"
        :b3.sync="kb3"
        :e0.sync="ke0"
        :e1.sync="ke1"
        :e2.sync="ke2"
        :e3.sync="ke3"
        :u0.sync="ku0"
        :u1.sync="ku1"
        :u2.sync="ku2"
        :u3.sync="ku3"
      />
      p
      <FundFormItem v-if="inside"
        :b0.sync="item.p.btc"
        :b1.sync="item.p.btc1"
        :b2.sync="item.p.btc2"
        :b3.sync="item.p.btc3"
        :e0.sync="item.p.eth"
        :e1.sync="item.p.eth1"
        :e2.sync="item.p.eth2"
        :e3.sync="item.p.eth3"
        :u0.sync="item.p.usdt"
        :u1.sync="item.p.usdt1"
        :u2.sync="item.p.usdt2"
        :u3.sync="item.p.usdt3"
      />
      <FundFormItem v-else
        :b0.sync="pb0"
        :b1.sync="pb1"
        :b2.sync="pb2"
        :b3.sync="pb3"
        :e0.sync="pe0"
        :e1.sync="pe1"
        :e2.sync="pe2"
        :e3.sync="pe3"
        :u0.sync="pu0"
        :u1.sync="pu1"
        :u2.sync="pu2"
        :u3.sync="pu3"
      />
      m
      <FundFormItem v-if="inside"
        :b0.sync="item.x.btc"
        :b1.sync="item.x.btc1"
        :b2.sync="item.x.btc2"
        :b3.sync="item.x.btc3"
        :e0.sync="item.x.eth"
        :e1.sync="item.x.eth1"
        :e2.sync="item.x.eth2"
        :e3.sync="item.x.eth3"
        :u0.sync="item.x.usdt"
        :u1.sync="item.x.usdt1"
        :u2.sync="item.x.usdt2"
        :u3.sync="item.x.usdt3"
      />
      <FundFormItem v-else
        :b0.sync="mb0"
        :b1.sync="mb1"
        :b2.sync="mb2"
        :b3.sync="mb3"
        :e0.sync="me0"
        :e1.sync="me1"
        :e2.sync="me2"
        :e3.sync="me3"
        :u0.sync="mu0"
        :u1.sync="mu1"
        :u2.sync="mu2"
        :u3.sync="mu3"
      />
      <div class="formItemContainer">
        <div class="sum">{{inside?item.sumBtc:sumBtc| numFormat('0,0.00000')}}</div>
        <div class="sum">{{inside?item.sumEth:sumEth| numFormat('0,0.00')}}</div>
        <div class="sum">{{inside?item.sumUsdt:sumUsdt| numFormat('0,0')}}</div>
      </div>
      <div class="percentageGroup">
        <img src="https://assets.coingecko.com/coins/images/1/large/bitcoin.png?1547033579" class="iconImg" alt="coin ico"/>
        <input type="text" v-model.number="item.btcPercentage" class="input" placeholder="btc %" required v-if="inside">
        <input type="text" v-model.number="btcPercentage" class="input" placeholder="btc %" required v-else>
        <br/>
        <img src="https://assets.coingecko.com/coins/images/279/large/ethereum.png?1595348880" class="iconImg" alt="coin ico"/>
        <input type="text" v-model.number="item.ethPercentage" class="input" placeholder="btc %" required v-if="inside">
        <input type="text" v-model.number="ethPercentage" class="input" placeholder="eth %" required v-else>
        <br/>
        <img src="https://assets.coingecko.com/coins/images/325/large/Tether-logo.png?1598003707" class="iconImg" alt="coin ico"/>
        <input type="text" v-model.number="item.usdtPercentage" class="input" placeholder="btc %" required v-if="inside">
        <input type="text" v-model.number="usdtPercentage" class="input" placeholder="usdt %" required v-else>
      </div>
      <br/>
      <br/>
      <img src="https://assets.coingecko.com/coins/images/1/large/bitcoin.png?1547033579" class="iconImg" alt="coin ico"/>
      <input type="text" v-model.number="item.btcRate" class="input" placeholder="btcRate" required v-if="inside">
      <input type="text" v-model.number="btcRate" class="input" placeholder="btcRate" required v-else>
      <img src="https://assets.coingecko.com/coins/images/279/large/ethereum.png?1595348880" class="iconImg" alt="coin ico"/>
      <input type="text" v-model.number="item.ethRate" class="input" placeholder="ethRate" required v-if="inside">
      <input type="text" v-model.number="ethRate" class="input" placeholder="ethRate" required v-else>
      <input type="text" v-model.number="item.date" class="input ml5" placeholder="date" required v-if="inside">
      <input type="text" v-model.number="date" class="input ml5" placeholder="date" required v-else>
      <span class="grey"> total:</span> {{(inside?totalInside:total)| numFormat('$ 0,0')}}
      <br/>
      <button class="fundButton" type="button" @click="updateFund" v-if="inside">save</button>
      <button class="fundButton" type="submit" v-else>add</button>
    </form>
  </section>
</template>

<script>
import FundFormItem from './FundFormItem';
import generateRandomId from '../../helpers/generateRandomId';
  export default {
    props:['item','inside'],
    components: { FundFormItem },
    data() {
      return {
        ab0:null,ab1:null,ab2:null,ab3:null,ae0:null,ae1:null,ae2:null,ae3:null,au0:null,au1:null,au2:null,au3:null,
        kb0:null,kb1:null,kb2:null,kb3:null,ke0:null,ke1:null,ke2:null,ke3:null,ku0:null,ku1:null,ku2:null,ku3:null,
        pb0:null,pb1:null,pb2:null,pb3:null,pe0:null,pe1:null,pe2:null,pe3:null,pu0:null,pu1:null,pu2:null,pu3:null,
        mb0:null,mb1:null,mb2:null,mb3:null,me0:null,me1:null,me2:null,me3:null,mu0:null,mu1:null,mu2:null,mu3:null,
        date:new Date().toLocaleString('en-US', {
          year: 'numeric',
          month: 'short',
          day: '2-digit'
        }),
        btcRate:null,
        ethRate:null,
        btcPercentage:null,
        ethPercentage:null,
        usdtPercentage:null,
      };
    },
    computed: {
      sumBtc(){
        return this.ab0+this.ab1+this.ab2+this.ab3+this.kb0+this.kb1+this.kb2+this.pb0+this.pb1+this.mb0
      },
      sumEth(){
        return this.ae0+this.ae1+this.ae2+this.ae3+this.ke0+this.ke1+this.ke2+this.pe0+this.pe1+this.me0
      },
      sumUsdt(){
        return this.au0+this.au1+this.au2+this.au3+this.ku0+this.ku1+this.ku2+this.pu0+this.pu1+this.mu0
      },
      total(){
        return this.sumBtc*this.btcRate+this.sumEth*this.ethRate+this.sumUsdt
      },
      totalInside(){
        const btc = ((this.item.a.btc?this.item.a.btc:0)
        +(this.item.a.btc1?this.item.a.btc1:0)
        +(this.item.a.btc2?this.item.a.btc2:0)
        +(this.item.a.btc3?this.item.a.btc3:0)
        +(this.item.k.btc?this.item.k.btc:0)
        +(this.item.k.btc1?this.item.k.btc1:0)
        +(this.item.k.btc2?this.item.k.btc2:0)
        +(this.item.k.btc3?this.item.k.btc3:0)
        +(this.item.p.btc?this.item.p.btc:0)
        +(this.item.p.btc1?this.item.p.btc1:0)
        +(this.item.p.btc2?this.item.p.btc2:0)
        +(this.item.p.btc3?this.item.p.btc3:0)
        +(this.item.x.btc?this.item.x.btc:0)
        +(this.item.x.btc1?this.item.x.btc1:0)
        +(this.item.x.btc2?this.item.x.btc2:0)
        +(this.item.x.btc3?this.item.x.btc3:0))*this.item.btcRate

        const eth = ((this.item.a.eth?this.item.a.eth:0)
        +(this.item.a.eth1?this.item.a.eth1:0)
        +(this.item.a.eth2?this.item.a.eth2:0)
        +(this.item.a.eth3?this.item.a.eth3:0)
        +(this.item.k.eth?this.item.k.eth:0)
        +(this.item.k.eth1?this.item.k.eth1:0)
        +(this.item.k.eth2?this.item.k.eth2:0)
        +(this.item.k.eth3?this.item.k.eth3:0)
        +(this.item.p.eth?this.item.p.eth:0)
        +(this.item.p.eth1?this.item.p.eth1:0)
        +(this.item.p.eth2?this.item.p.eth2:0)
        +(this.item.p.eth3?this.item.p.eth3:0)
        +(this.item.x.eth?this.item.x.eth:0)
        +(this.item.x.eth1?this.item.x.eth1:0)
        +(this.item.x.eth2?this.item.x.eth2:0)
        +(this.item.x.eth3?this.item.x.eth3:0))*this.item.ethRate

        const usdt = (this.item.a.usdt?this.item.a.usdt:0)
        +(this.item.a.usdt1?this.item.a.usdt1:0)
        +(this.item.a.usdt2?this.item.a.usdt2:0)
        +(this.item.a.usdt3?this.item.a.usdt3:0)
        +(this.item.k.usdt?this.item.k.usdt:0)
        +(this.item.k.usdt1?this.item.k.usdt1:0)
        +(this.item.k.usdt2?this.item.k.usdt2:0)
        +(this.item.k.usdt3?this.item.k.usdt3:0)
        +(this.item.p.usdt?this.item.p.usdt:0)
        +(this.item.p.usdt1?this.item.p.usdt1:0)
        +(this.item.p.usdt2?this.item.p.usdt2:0)
        +(this.item.p.usdt3?this.item.p.usdt3:0)
        +(this.item.x.usdt?this.item.x.usdt:0)
        +(this.item.x.usdt1?this.item.x.usdt1:0)
        +(this.item.x.usdt2?this.item.x.usdt2:0)
        +(this.item.x.usdt3?this.item.x.usdt3:0)

        return btc+eth+usdt
      },
    },
    methods:{
      addFundWeek(){
        if(this.inside){
          return
        }
        const item = {
          a: {
            btc: this.ab0,
            btc1: this.ab1,
            btc2: this.ab2,
            btc3: this.ab3,
            eth: this.ae0,
            eth1: this.ae1,
            eth2: this.ae2,
            eth3: this.ae3,
            usdt: this.au0,
            usdt1: this.au1,
            usdt2: this.au2,
            usdt3: this.au3,
          },
          k: {
            btc: this.kb0,
            btc1: this.kb1,
            btc2: this.kb2,
            btc3: this.kb3,
            eth: this.ke0,
            eth1: this.ke1,
            eth2: this.ke2,
            eth3: this.ke3,
            usdt: this.ku0,
            usdt1: this.ku1,
            usdt2: this.ku2,
            usdt3: this.ku3,
          },
          p: {
            btc: this.pb0,
            btc1: this.pb1,
            btc2: this.pb2,
            btc3: this.pb3,
            eth: this.pe0,
            eth1: this.pe1,
            eth2: this.pe2,
            eth3: this.pe3,
            usdt: this.pu0,
            usdt1: this.pu1,
            usdt2: this.pu2,
            usdt3: this.pu3,
          },
          x: {
            btc: this.mb0,
            btc1: this.mb1,
            btc2: this.mb2,
            btc3: this.mb3,
            eth: this.me0,
            eth1: this.me1,
            eth2: this.me2,
            eth3: this.me3,
            usdt: this.mu0,
            usdt1: this.mu1,
            usdt2: this.mu2,
            usdt3: this.mu3,
          },
          btcRate:this.btcRate,
          ethRate:this.ethRate,
          sumBtc:this.sumBtc,
          sumEth:this.sumEth,
          sumUsdt:this.sumUsdt,
          total:this.total,
          date:this.date,
          id:generateRandomId(),
          btcPercentage:this.btcPercentage,
          ethPercentage:this.ethPercentage,
          usdtPercentage:this.usdtPercentage,
        }
        this.$store.commit('addFundWeek', {item:item})//переделка под старый формат
        this.resetForm();
      },
      resetForm(){
        this.ab0=null;this.ab1=null;this.ab2=null;this.ab3=null;this.ae0=null;this.ae1=null;this.ae2=null;this.ae3=null;this.au0=null;this.au1=null;this.au2=null;this.au3=null;
          this.kb0=null;this.kb1=null;this.kb2=null;this.kb3=null;this.ke0=null;this.ke1=null;this.ke2=null;this.ke3=null;this.ku0=null;this.ku1=null;this.ku2=null;this.ku3=null;
          this.pb0=null;this.pb1=null;this.pb2=null;this.pb3=null;this.pe0=null;this.pe1=null;this.pe2=null;this.pe3=null;this.pu0=null;this.pu1=null;this.pu2=null;this.pu3=null;
          this.mb0=null;this.mb1=null;this.mb2=null;this.mb3=null;this.me0=null;this.me1=null;this.me2=null;this.me3=null;this.mu0=null;this.mu1=null;this.mu2=null;this.mu3=null;
          this.date=null;
          this.btcRate=null;
          this.ethRate=null;
          this.btcPercentage=null;
          this.ethPercentage=null;
          this.usdtPercentage=null;
      },
      updateFund(){
        this.$store.dispatch('updateFB',{name:'fundList',info:this.$store.state.isAK?'infoAK':'infoMain'});
      }
    }
  };
</script>
<style scoped>
  .form{
    max-width:500px;
    margin:20px auto 20px auto;
    padding: 10px;
  }
  .input{
    width:100px;
    background-color: transparent;
    border:1px solid rgba(255,255,255,0.2);
    color:rgba(255,255,255,0.8);
  }
  .fundButton{
    width:50px;
    background-color: #17181b;
    border:1px solid grey;
    color:grey;
    margin: 20px auto 0 auto;
    display: block;
  }
  .fundButton:hover{
    cursor:pointer;
    color:white;
  }
  .iconImg{
    width:15px;
  }
  .formItemContainer{
    display:flex;
    justify-content: space-evenly;
    margin-bottom: 10px;
  }
  .sum{
    font-size: 12px;
    color:rgba(255,255,255,0.4);;
  }
  .percentageGroup{
    display: flex;
    justify-content: center;
